import { FC } from "react"
import { FormattedMessage } from 'react-intl'

type Props = {
    artistName: string
    ownershipOffered: string
}

export const Financial: FC<Props> = ({ artistName, ownershipOffered }) => {
    return (
        <>
            <div className="mx-10 mb-10" style={{ marginTop: '25px' }}>
                <span className="flex-start asset-type"><FormattedMessage id="COLLECTOR_ASSET_DETAIL.FINANCIAL" /></span>

                <div className="d-flex flex-column mt-5">
                    <div className="d-flex justify-content-between mt-2">
                        <span className="fs-6"><FormattedMessage id="COLLECTOR_ASSET_DETAIL.OWNERSHIP" /></span>
                        <span className="fs-6">{parseFloat(ownershipOffered)}%</span>
                    </div> 
                    <div className="d-flex justify-content-between mt-2">
                        <span className="fs-6"><FormattedMessage id="COLLECTOR_ASSET_DETAIL.ASSET_SELLERS" /></span>
                        <span className="fs-6">{artistName}</span>
                    </div>
                </div>
            </div>
        </>
    )
}