import { FC } from "react"
import { blockchainAbbr } from "../../yourCollections/common/common"
import { FormattedMessage } from 'react-intl'

type Props = {
    blockchain: string
    collectionID: string
}

export const Legal: FC<Props> = ({ blockchain, collectionID }) => {
    return (
        <>
            <div className="mx-10" style={{ marginTop: '25px' }}>
                <span className="flex-start asset-type"><FormattedMessage id="ADMIN_COLLECTION_PREVIEW.LEGAL" /></span>

                <div className="d-flex flex-column mt-5">
                    <div className="d-flex justify-content-between mt-2">
                        <span className="fs-6"><FormattedMessage id="COLLECTION_DETAIL_PAGE.CONTRACT_ADDRESS" /></span>
                        <span className="fs-6">0xa6c8...13ce</span>
                    </div>
                    <div className="d-flex justify-content-between mt-2">
                        <span className="fs-6"><FormattedMessage id="EXPLORE.SIDE_NAV.BLOCKCHAIN" /></span>
                        <span className="fs-6">{blockchainAbbr[blockchain]}</span>
                    </div>
                    <div className="d-flex justify-content-between mt-2">
                        <span className="fs-6"><FormattedMessage id="COLLECTOR_ASSET_DETAIL.TOKEN_STANDARD" /></span>
                        <span className="fs-6">{blockchain == 'ethereum' ? 'ERC1155' : 'KIP-37'}</span>
                    </div>
                    <div className="d-flex justify-content-between mt-2">
                        <span className="fs-6"><FormattedMessage id="ADMIN_COLLECTION_PREVIEW.LEGAL" /></span>
                        <a 
                            href={`${process.env.REACT_APP_LEGAL_DOCUMENTS_URL}/${collectionID + '_legal_docs.pdf'}`}
                            className="text-decoration-none cursor-pointer fs-6"
                        >
                            <FormattedMessage id="ADMIN_COLLECTION_PREVIEW.READ_NOW" />
                            <span>
                            <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor" aria-hidden="true" className="cursor-pointer ml-1 h-svg w-svg">
                                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M13 7l5 5m0 0l-5 5m5-5H6"></path>
                            </svg>
                            </span>
                        </a>
                    </div>
                </div>
            </div>
        </>
    )
}