import { useState, useEffect } from "react"
import { useLocation } from "react-router-dom"
import { CollectorAssetDetailCard } from './components/CollectorAssetDetailCard'
import { Legal } from './components/Legal'
import { Drop } from './components/Drop'
import { Financial } from './components/Financial'
import { AssetData } from "./models"
import { getTierTokenBenefits } from "./api"
import { FormattedMessage } from "react-intl"

const CollectorAssetDetail = () => {

    const { state }: any = useLocation()
    const [assetData] = useState<AssetData>(state)
    const [stdTokenBenefits, setStdTokenBenefits] = useState<Array<any>>([])
    const [custTokenBenefits, setCustTokenBenefits] = useState<Array<any>>([])

    useEffect(() => {
        if(assetData.collectionID && assetData.tier) {
            getTierTokenBenefits(assetData.collectionID, assetData.tier)
            .then((response) => {
                if(response.data.success) {
                    setStdTokenBenefits(JSON.parse(response.data.tier_benefits.replace(/&quot;/g, '"')).stdBenefits)
                    setCustTokenBenefits(JSON.parse(response.data.tier_benefits.replace(/&quot;/g, '"')).custBenefits)
                }
            })
        }
    }, [])

    const handleOpenseaRoute = (blockchain: string) => {
        if(blockchain == 'ethereum') {
            window.location.href = 'https://testnets.opensea.io/collection/musician-nft-v3'
        } else {
            window.location.href = 'https://testnets.opensea.io/collection/themusician-musicasset'
        }
    }

    return (
        <>
            <div className="container-fluid bg-white">
                <div className="row g-5 g-xl-8 mx-3 pb-10">
                    <div className="col-xl-4">
                        <CollectorAssetDetailCard 
                            assetData={assetData}
                        />
                        <div className='d-flex justify-content-ceter mt-5'>
                            <button 
                                style={{ 
                                    background: '#ffd600',
                                    borderColor: '#0000',
                                    color: '#000'
                                 }}
                                className='black-button mx-auto py-2 mt-5'
                                onClick={() => handleOpenseaRoute(assetData.blockchain)}
                            >
                                <FormattedMessage id="COLLECTOR_ASSET_DETAIL.BUY_ON_OPENSEA" />
                            </button>
                        </div>
                    </div>
                    <div className='col-xl-8'>
                        <div className='d-flex flex-column rounded' style={{ border: '1px solid grey' }}>
                            <article className='d-flex flex-column mt-10'>
                                <span className='flex-start asset-type mx-10'><FormattedMessage id="COLLECTOR_ASSET_DETAIL.TOKEN" /></span>

                                <div className='separator my-2 mb-10'></div>

                                {stdTokenBenefits.map((data) => (
                                    <div className='d-flex justify-content-between mx-10 mb-10'>
                                        <div className='d-flex'>
                                            <div 
                                                style={{
                                                    height: '60px',
                                                    width: '60px',
                                                    border: '1px solid grey',
                                                    background: '#eeeeee'
                                                }}
                                                className='d-flex justify-content-center rounded'
                                            >
                                                <span style={{ alignSelf: 'center', fontSize: '25px' }}>ðŸ’¬</span>
                                            </div>
                                            <span className='my-auto ms-3 fs-5'>{data}</span>
                                        </div>
                                    </div>
                                ))}

                                {custTokenBenefits.map((data) => (
                                    <div className='d-flex justify-content-between mx-10'>
                                        <div className='d-flex'>
                                            <div 
                                                style={{
                                                    height: '60px',
                                                    width: '60px',
                                                    border: '1px solid grey',
                                                    background: '#eeeeee'
                                                }}
                                                className='d-flex justify-content-center rounded'
                                            >
                                                <span style={{ alignSelf: 'center', fontSize: '25px' }}>ðŸ’¬</span>
                                            </div>
                                            <span className='my-auto ms-3 fs-5'>{data}</span>
                                        </div>
                                    </div>
                                ))}
                                
                            </article>

                            <div className='separator mt-10'></div>

                            <Legal blockchain={assetData.blockchain} collectionID={assetData.collectionID} />

                            <div className='separator mt-10'></div>

                            <Drop 
                                dropDate={assetData.dropDate}
                                artistName={assetData.artistName}
                                collectionName={assetData.collectionName}
                            />

                            <div className='separator mt-10'></div>

                            <Financial 
                                ownershipOffered={assetData.ownershipOffered}
                                artistName={assetData.artistName}
                            />
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default CollectorAssetDetail