import { useState, FC } from "react"
import { useLocation } from "react-router-dom"
import { AssetData } from '../models'
import { FormattedMessage } from 'react-intl'

type Props = {
    assetData: AssetData
}

const tiers: any = {
    '0': "Gold",
    '1': "Platinum",
    '2': "Diamond",
}

export const CollectorAssetDetailCard: FC<Props> = ({ assetData }) => {

    return (
        <>
            <div 
              className="card cust-shadow"
              style={{ 
                    boxShadow: '0 10px 16px 0 rgb(0 0 0 / 20%), 0 6px 20px 0 rgb(0 0 0 / 19%) !important',
                    borderRadius: '5px',
              }}
            >
                <a 
                    href={`drop-detail`}
                    style={{ padding: '0px' }}
                    className="card-body d-flex flex-column bg-white rounded"
                >
                    <div className="text-center w-100">
                        <img 
                            // src='https://royal-io.imgix.net/ea162b1c-ecdb-4fea-b916-b16796bb932b.svg'
                            src={assetData.tierImage} 
                            className="w-100 px-4 py-5" 
                            style={{ 
                                borderTopLeftRadius: '5px',
                                borderTopRightRadius: '5px',
                                textAlign: 'center'
                            }}
                        />
                    </div>
                    <div className="d-flex flex-column">
                        <div className="fw-bolder mx-6 mt-3">
                            <h3>{assetData.collectionName}</h3>
                        </div>
                        <div className="fw-bolder mx-6 mt-3">
                            <h4>{assetData.artistName}</h4>
                        </div>
                        <div className="d-flex justify-content-between mx-6 my-3">
                            <span className="fs-5 text-dark">{tiers[assetData.tier]}</span>
                            <span className="fs-5 text-dark">{assetData.totalOwns}/{assetData.totalQuantity}</span>
                        </div>
                        <div className="d-flex py-3 mx-5 mb-4">
                            <div>
                                <img 
                                    className="w-50px"
                                    style={{ borderRadius: '50%' }}
                                    src={`${process.env.REACT_APP_API_USER_IMAGE_URL}/${assetData.collectorImage}`} 
                                    alt=''
                                />
                            </div>
                            <div className="mt-2 ms-3">
                                <span style={{ color: 'black' }}>{assetData.collectorName}</span><br/>
                                <span className="text-muted"><FormattedMessage id="COLLECTOR_ASSET_DETAIL.OWNER" /></span>
                            </div>
                        </div>
                    </div>
                </a>
            </div>
        </>
    )
}