import { FC } from "react"
import { FormattedMessage } from 'react-intl'

type Props = {
    dropDate: string
    artistName: string
    collectionName: string
}

export const Drop: FC<Props> = ({ dropDate, artistName, collectionName }) => {
    return (
        <>
            <div className="mx-10" style={{ marginTop: '25px' }}>
                <span className="flex-start asset-type"><FormattedMessage id="COLLECTOR_ASSET_DETAIL.DROP" /></span>

                <div className="d-flex flex-column mt-5">
                    <div className="d-flex justify-content-between mt-2">
                        <span className="fs-6"><FormattedMessage id="COLLECTOR_ASSET_DETAIL.DROP_DATE" /></span>
                        <span className="fs-6">{dropDate}</span>
                    </div> 
                    <div className="d-flex justify-content-between mt-2">
                        <span className="fs-6"><FormattedMessage id="COLLECTOR_ASSET_DETAIL.COLLABORATORS" /></span>
                        <span className="fs-6">{artistName}</span>
                    </div>
                    <div className="d-flex justify-content-between mt-2">
                        <span className="fs-6"><FormattedMessage id="ADMIN_COLLECTION_PREVIEW.EDITION" /></span>
                        <span className="fs-6">{collectionName}</span>
                    </div>
                </div>
            </div>
        </>
    )
}