import axios from "axios";

const GET_COLLECTOR_DATA_URL = `${process.env.REACT_APP_API_URL}/get-collector-data.php`;
const GET_TIER_TOKEN_BENEFITS_URL = `${process.env.REACT_APP_API_URL}/get-tier-benefits-data.php`;
const GET_WALLET_ADDRESSES_FOR_USER_URL = `${process.env.REACT_APP_API_URL}/get-wallet-addresses-for-user.php`;

export function getCollectorData(walletAddress: string) {
    return axios({
        method: 'post',
        url: GET_COLLECTOR_DATA_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            walletAddress
        }
    })
}

export function getTierTokenBenefits(collectionID: string, tier: string) {
    return axios({
        method: 'post',
        url: GET_TIER_TOKEN_BENEFITS_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            collectionID,
            tier
        }
    })
}

export function getWalletAddressesForUser(userID: string) {
    return axios({
        method: 'post',
        url: GET_WALLET_ADDRESSES_FOR_USER_URL,
        headers: {
            'content-type': 'application/x-www-form-urlencoded',
        },
        data: {
            userID
        }
    })
}
